.search-box{
    border:0.5px solid gainsboro;
    border-radius: 20px;
    padding: 10px;
    width: 90%;
    margin-left: 5%;
    outline: none;
    transition: 200ms ease-in;
}

input[type=search]:focus {
    border-style:none;    
    border-radius: 0px;
    box-shadow: 2px 2px 5px gray;
    width: 100%;
    margin-left: 0%;
    font-style: italic;
}