

#Clouds{
    background: url('./Assets/cloud-dark.jpg');
    background-position: center;
    background-size: cover;
    position: absolute;
    width: 100%;
    height:100%;
    transition: 1s ease;
}

#Clear{
    background: url('./Assets/cloud-bright.jpg');
    background-position: right;
    background-size: cover;
    position: absolute;
    width: 100%;
    height:100%;
    transition: 1s ease;
}

#Mist{
    background: url('./Assets/snow.jpg');
    background-position: right;
    background-size: cover;
    position: absolute;
    width: 100%;
    height:100%;
    transition: 1s ease;
}

#Rain{
    background: url('./Assets/rain.jpg');
    background-position: right;
    background-size: cover;
    position: absolute;
    width: 100%;
    height:100%;
    transition: 1s ease-in;
}